import { getBaseName } from '../common/util';
import { __ } from '../common/sakurairo_global';
import { importExternal } from '../common/npmLib';
const bgvideo = document.getElementById("bgvideo");
const videoList = _iro.movies.name?.split(",") || []; // 
let unplayedIndex = new Array(videoList.length).fill(0).map((_, index) => index);
let aplayersToResume = [];
//#region 
const _getNextRandomVideo = () => {
    if (unplayedIndex.length == 0) {
        unplayedIndex = new Array(videoList.length).fill(0).map((_, index) => index);
    }
    const nextIndex = Math.floor(Math.random() * unplayedIndex.length);
    return videoList[unplayedIndex.splice(nextIndex, 1)[0]];
};
function getVideo() {
    const video_stu = document.getElementsByClassName("video-stu")[0];
    const fileName = _getNextRandomVideo(); // 
    video_stu.innerHTML = __(" ...");
    video_stu.style.bottom = "0px";
    //_iro.movies
    bgvideo.setAttribute("src", new URL(fileName, _iro.movies.url || location.origin).toString());
    bgvideo.setAttribute("video-name", getBaseName(fileName));
}
/**
 * 
 */
function splay() {
    const video_btn = document.getElementById("video-btn");
    if (video_btn) {
        video_btn.classList.add("video-pause");
        video_btn.classList.remove("video-play");
        video_btn.style.display = "";
    }
    try {
        document.querySelector(".video-stu").style.bottom = "-100px";
        document.querySelector(".focusinfo").style.top = "-999px";
        if (_iro.float_player_on) {
            import('./aplayer').then(({ pauseAllPlayer }) => {
                aplayersToResume = pauseAllPlayer();
                bgvideo.play();
            });
            return;
        }
    }
    catch (e) {
        console.warn(e);
    }
    bgvideo.play();
}
/**
 * 
 */
function spause() {
    const video_btn = document.getElementById("video-btn");
    if (video_btn) {
        video_btn.classList.add("video-play");
        video_btn.classList.remove("video-pause");
    }
    try {
        document.querySelector(".focusinfo").style.top = "49.3%";
    }
    catch { }
    bgvideo.pause();
    for (const player of aplayersToResume) {
        player.play();
    }
}
/**
 *  - 
 */
export function liveplay() {
    if (bgvideo && bgvideo.oncanplay != undefined && document.querySelector(".haslive")) { // 
        if (document.querySelector(".videolive")) { // 
            splay();
        }
    }
}
export function livepause() {
    if (bgvideo && bgvideo.oncanplay != undefined && document.querySelector(".haslive")) { // 
        spause();
        const video_stu = document.getElementsByClassName("video-stu")[0];
        video_stu.style.bottom = "0px";
        video_stu.innerHTML = __("...");
    }
}
export function coverVideo() {
    const video_btn = document.getElementById("video-btn");
    if (video_btn)
        video_btn.addEventListener("click", function () {
            if (this.classList.contains("loadvideo")) {
                this.classList.add("video-pause");
                this.classList.remove("loadvideo");
                getVideo();
                bgvideo.oncanplay = () => {
                    splay();
                    document.getElementById("video-add").style.display = "block";
                    video_btn.classList.add("videolive", "haslive"); // MDZZ
                };
            }
            else {
                if (this.classList.contains("video-pause")) {
                    spause();
                    video_btn.classList.remove("videolive");
                    document.getElementsByClassName("video-stu")[0].style.bottom = "0px";
                    document.getElementsByClassName("video-stu")[0].innerHTML = __("...");
                }
                else {
                    splay();
                    video_btn.classList.add("videolive"); // 
                }
            }
            bgvideo.onended = function () {
                bgvideo.setAttribute("src", "");
                document.getElementById("video-add").style.display = "none";
                document.querySelector(".focusinfo").style.top = "49.3%";
                if (video_btn) {
                    video_btn.classList.add("loadvideo");
                    video_btn.classList.remove("video-pause", "videolive", "haslive");
                    if (_iro.movies.loop) {
                        video_btn.click();
                    }
                }
            };
        });
    const video_add = document.getElementById("video-add");
    if (video_add)
        video_add.addEventListener("click", getVideo);
}
//#endregion
export async function coverVideoIni() {
    initHLS();
    lazyloadPatch();
}
function canPlayHandler() {
    this.poster = '';
}
/**
 * poster
 */
async function lazyloadPatch() {
    document.querySelectorAll('video.lazyload')
        .forEach(video => video.addEventListener('canplay', canPlayHandler));
}
async function initHLS() {
    const videos = document.querySelectorAll('video.hls');
    if (videos.length == 0)
        return;
    //
    if (videos[0].canPlayType('application/vnd.apple.mpegurl')) {
        for (const video of videos) {
            video.src = video.dataset.src || video.src;
            video.autoplay = true;
        }
    }
    else {
        if (!window.Hls) {
            try {
                if (_iro.ext_shared_lib) {
                    await importExternal('dist/hls.light.min.js', 'hls.js');
                }
                else {
                    //@ts-ignore
                    const { default: Hls } = await import('hls.js/dist/hls.light.js');
                    window.Hls = Hls;
                }
            }
            catch (reason) {
                console.warn('Hls load failed: ', reason);
            }
        }
        if (!Hls.isSupported())
            console.error('Hls: Media Source Extensions is unsupported.');
        for (const video of videos) {
            const hls = new Hls();
            hls.loadSource(video.dataset.src || video.src);
            hls.attachMedia(video);
            hls.on(Hls.Events.MANIFEST_PARSED, () => {
                video.play();
            });
        }
    }
}
