const STORAGE_KEY = 'sakurairo_prefer_cdn';
const CDN_LIST = [
    /*     ["https://jscdn.host/release/ucode/", "/", "/", ""],
        ["https://cdnjs.cloudflare.com/ajax/libs/", "/", "/", ""],
     */
    ["https://cdn.jsdelivr.net/npm/", "@", "/", ""],
    ["https://unpkg.com/", "@", "/", ""],
    //TODO:cdn
];
function getPreferCDNIndex() {
    const config = localStorage.getItem(STORAGE_KEY);
    if (config) {
        const num = parseInt(config);
        if (isNaN(num) || num >= CDN_LIST.length) {
            //
            localStorage.removeItem(STORAGE_KEY);
            return 0;
        }
        return num;
    }
    else {
        return 0;
    }
}
const currentCDNIndex = getPreferCDNIndex();
const currentCDN = { raw: CDN_LIST[currentCDNIndex] };
/**
 * CDNnpm
 * @author KotoriK
 * @param relativePath npm
 * @param packageName npm
 * @param version npmpackage.jsonpackage_info.jslatest
 * @returns cdnurl
 */
export const resolvePath = (relativePath, packageName, version) => String.raw(currentCDN, packageName, version || PKG_INFO[packageName] || 'latest', relativePath);
export const importExternal = (path, packageName, version) => {
    const id = `${packageName}${version ? '@' + version : ''}${path}`;
    if (document.getElementById(id)) { // 
        return Promise.resolve();
    }
    const script = document.createElement('script');
    script.id = id;
    script.src = resolvePath(path, packageName, version);
    script.async = true;
    //TODO: 
    return new Promise((resolve, reject) => {
        script.onload = () => resolve();
        script.onerror = () => {
            script.remove(); // 
            reject(new Error(packageName + ""));
        };
        document.body.append(script);
    }).finally(() => {
        script.onload = script.onerror = null; //ie
    });
};
